.qa-tooltip-container {
  position: relative; }

.qa-tooltip {
  position: absolute;
  left: 50%;
  top: 0;
  font-weight: 200;
  white-space: nowrap;
  z-index: 5;
  font-size: 14px;
  padding: 10px;
  background-color: #FFFFFF;
  font-family: 'Lato', sans-serif;
  -webkit-transform: translate(-50%, -100%);
      -ms-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  -webkit-box-shadow: 0 4px 15px rgba(21, 35, 52, 0.07), 0 0 2px rgba(21, 35, 52, 0.1);
          box-shadow: 0 4px 15px rgba(21, 35, 52, 0.07), 0 0 2px rgba(21, 35, 52, 0.1); }



