.list {
  overflow: hidden;
  border-radius: 5px;
  -webkit-box-shadow: 0 4px 15px rgba(21, 35, 52, 0.07), 0 0 2px rgba(21, 35, 52, 0.1);
          box-shadow: 0 4px 15px rgba(21, 35, 52, 0.07), 0 0 2px rgba(21, 35, 52, 0.1); }

.list-item {
  border-bottom: 1px solid rgba(229, 231, 232, 0.5); }

.btn {
  font-weight: 200;
  padding: 8px 15px; }
  .btn.btn-primary {
    background-color: #0067F4;
    border: none;
    -webkit-box-shadow: 0 2px 5px rgba(0, 103, 244, 0.2), 0 0 2px rgba(0, 103, 244, 0.1);
            box-shadow: 0 2px 5px rgba(0, 103, 244, 0.2), 0 0 2px rgba(0, 103, 244, 0.1); }
    .btn.btn-primary:hover {
      background-color: #288fff; }

.font-weight-lighter {
  font-weight: 200; }

body, html {
  margin: 0;
  color: #152334;
  font-family: 'Lato', sans-serif; }

h1, h2, h3, h4, h5 {
  font-family: 'Oswald', sans-serif; }

input, textarea {
  outline: none; }
  input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-weight: 200;
    color: #caced0; }



